body {
  font: 70px "Century Gothic", Futura, sans-serif;
  margin: 100px;
  top: 50%;
  left: 50%;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 50px;
}

.square {
  background: #fff;
  border: 5px solid #999;
  float: left;
  font-size: 120px;
  font-weight: bold;
  line-height: 170px;
  height: 170px;
  margin-right: -5px;
  margin-top: -5px;
  padding: 0;
  text-align: center;
  width: 170px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

